.section1 {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100vh;
    /*background: linear-gradient(45deg, rgb(2, 12, 0), rgb(4, 52, 4), rgb(32, 152, 90) );*/
    background-color: #161618;
}

.div1_da_section_1{
    display: flex;
    flex-direction: column;
    justify-items: center;
    width: 100%;
}

.div2_da_section_1 {
    display: flex;
    justify-content: center;
    width: 100%;
    margin-top: 20px;
    margin-bottom: 20px; 
}

.foto_de_apresentacao {
    width: 300px;
    height: 300px;
    border-radius: 50%;
    border-bottom: 3px solid #581de3;
    border-right: 3px solid #581de3;
    border-left: 3px solid #581de3;
    border-top: 3px solid #581de3;
}

.titulo_da_section_1 {
    font-size: 60px;
    text-align: center;
    padding-top: 40px;
    padding-bottom: 20px;
}

.subTitulo_da_section_1 {
    text-decoration: none;
    color: rgb(255, 255, 255);
    text-align: center;
    padding-bottom: 20px;

}

.sub_titulo_texto_section_1 {
    color: rgb(12, 186, 255);
    text-align: center;
    padding-bottom: 20px;
}

.botao_download {
    cursor: pointer;
    text-decoration: none;
    display: block;
    margin-left: auto;
    margin-right: auto;
    padding: 2px;
    background-color: rgb(245, 159, 0);
    width: 200px;
    height: 50px;
    border-radius: 10px;
    
}

.botao_download a {
    text-decoration: none;
    color: black;
    font-size: 16px;
    font-weight: 800;
}
.section2 {
    display: flex;
    flex-direction: row;
    background-color: #2a2a5f;
    justify-content: center;
    width: 100%;
    height: 100vh;     
}
.section_2_titulo{
    font-size: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    padding-top: 100px;
    padding-bottom: 100px;
    width: 100%;
}
.section2ImagemTecnologia{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.section3 {
    display: flex;
    flex-direction: row;
    margin-bottom: 200px;
    justify-content: center;
    height: 100vh;  
    width: 100%;   
}
.section_3_titulo{
    font-size: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    padding-top: 100px;
    padding-bottom: 100px;
    width: 100%;
    color: rgb(255, 255, 255);
}

.section3ImagemDeProjetos{
    width: 300px;
    height: 300px;
    
}
.divProjetos{
    background-color:#1e1e43;
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    
    
}
.divProjetosFotos{
    background-color: #1e1e43;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    

}
.divProjetosFotos img{
    border-radius: 10%;
    margin-right: 10px;
        
}
/*responsivo*/
@media (min-width: 200px) and (max-width: 1024px){
    .section1{
        display: flex;
        flex-direction: column;
        
    }
    .div2_da_section_1 {
        display: flex;
        justify-content: center;
        width: 100%;
        margin-top: 20px;
        margin-bottom: 20px; 
    }
    .divProjetosFotos img{
        width: 100px;
        height: 100px;
        border-radius: 2px;

    }
    .foto_de_apresentacao {
        width: 150px;
        height: 150px;
        border-radius: 50%;
        border-bottom: 3px solid #581de3;
        border-right: 3px solid #581de3;
        border-left: 3px solid #581de3;
        border-top: 3px solid #581de3;
    }
    .section3{
        width: 100%;
        height: 100vh;
        margin-bottom: -1px;
    }
}