.footer{
    background-color: #121214;
    width: 100%;
    height: 100px; 
    display: flex;
    justify-content: center;
    align-items: center; 
    padding-top: 20px;
    list-style: none;
    font-size: 15px;
    
}

.marginEsquerda{
    margin: 100px;
    background-color: #121214;
    border-radius: 5px;
    width: 50%;
    height: 35px;
    text-decoration: none;
    text-align: center;
    justify-content: space-around;
    display: flex;
}
.marginEsquerda a{
    display: flex;
    width: 100px;
}


.corDaFonte{
    color: rgb(255, 255, 255);
    font-size: 20px;
    text-decoration: none;
    border-radius: 5px;
    border: 1px solid #9466FF;
    padding: 5px;
}

.corDaFonte:hover{
    background-color: #9466FF;
    transition-duration: 0.7s;
}
/*Responsivo*/
@media (min-width: 200px) and (max-width: 500px){
    .footer{
        width: 100%;
        display: flex;
        flex-direction: column;
        
        
    }
    .corDaFonte{
        width: 400px;

    }
    .marginEsquerda{
        display: flex;
        justify-content: space-evenly;
        width: 80%;
        
        
    }
    .marginEsquerda a{
        display: flex;
        width: 100%;
        justify-content: center;
        
        

    }
}


