.container{
    width: 100%;
    height: 100%;
    padding-top: 100px;
    background-color: #161618;
    display: flex;
    justify-items: center;
}

.fotoPerfil{
    display: flex;
    justify-content: center;
    width: 40%;
    padding: 50px;
    
}

.fotoPerfil img{
    width: 300px;
    height: 300px;
    border-radius: 50%;
    border-bottom: 3px solid #581de3;
    border-right: 3px solid #581de3;
    border-left: 3px solid #581de3;
    border-top: 3px solid #581de3;
    
}

.divTextoSobre{
    width: 50%;
    height: 100vh;
    display: flex;
    text-align: justify;
    margin-left: 40px;  
}

.divTextoSobre p{
    font-size: 22px;
}

/*Responsividade*/
@media (min-width: 400px) and (max-width: 700px){
    .divTextoSobre p{
        font-size: 20px;
    }
    .fotoPerfil img{
        width: 200px;
        height: 200px;
    }
}
@media (min-width: 200px) and (max-width: 400px){
    .divTextoSobre p{
        font-size: 15px;
    }
    .fotoPerfil img{
        width: 200px;
        height: 200px;
    }
}
